'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { Hero } from "./components/Hero";
import { Trust } from "./components/Trust";
import { HowItWorks } from "./components/HowItWorks";
import { LoanCalculator } from "./components/LoanCalculator";
import { ApplicationForm } from "./components/ApplicationForm";
import { Testimonials } from "./components/Testimonials";
import { Contact } from "./components/Contact";
import { Footer } from "./components/Footer";
import { MinimalHeader } from "./components/MinimalHeader";
import { SectionNavigator } from "./components/SectionNavigator";
import { AdminPanel } from "./components/AdminPanel";
import { AdminAuth } from "./components/AdminAuth";
import { DeploymentSteps } from "./components/DeploymentSteps";
import { DeploymentChecklist } from "./components/DeploymentChecklist";
import { LanguageProvider } from "./contexts/LanguageContext";
import { Button } from "./components/ui/button";
import { Shield, Home, Rocket, CheckSquare } from "lucide-react";
import { logEnvironmentConfig, validateEnvironment, appConfig } from "./config/environment";
import "./loan.css";

// Wrap environment access in try-catch to prevent errors during SSR
let safeAppConfig = appConfig;
try {
  safeAppConfig = appConfig;
} catch (error) {
  console.warn('Error loading app config, using defaults');
  safeAppConfig = {
    env: 'development' as const,
    domain: 'localhost:3000',
    mainSiteDomain: 'https://yellowmetal.co',
    apiBaseUrl: 'http://localhost:3000/api',
    appUrl: 'http://localhost:3000',
    supabase: { url: '', anonKey: '' },
    notion: { apiToken: '', databaseId: '' },
    analytics: { gaTrackingId: '' },
    features: { adminPanel: true, multilingual: true, calculator: true },
    goldRateApi: { endpoint: '', apiKey: '' },
    security: { adminSecretKey: 'default_dev_key' }
  };
}

export default function LoanPage() {
  const [isAdminMode, setIsAdminMode] = useState(false);
  const [isDeploymentMode, setIsDeploymentMode] = useState(false);
  const [deploymentView, setDeploymentView] = useState<'steps' | 'checklist'>('steps');
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // Initialize app and check environment
  useEffect(() => {
    try {
      // Only run in browser
      if (typeof window === 'undefined') return;
      
      // Log environment configuration in development (with error handling)
      try {
        if (safeAppConfig?.env === 'development') {
          logEnvironmentConfig();
        }
      } catch (e) {
        // Silently fail if logging fails
      }
      
      // Validate critical environment variables (only warn in production)
      try {
        if (safeAppConfig?.env === 'production') {
          const validation = validateEnvironment();
          if (!validation.isValid) {
            console.warn('‚ö†Ô∏è Some environment variables are missing:', validation.missingVars);
          }
        }
      } catch (e) {
        // Silently fail if validation fails
      }
      
      // Check for admin mode from URL parameters
      const urlParams = new URLSearchParams(window.location.search);
      const adminParam = urlParams.get('admin');
      const deployParam = urlParams.get('deploy');
      
      if (adminParam === 'true') {
        setIsAdminMode(true);
      }
      
      if (deployParam === 'true') {
        setIsDeploymentMode(true);
      }
    } catch (error) {
      console.error('Error initializing loan page:', error);
      // Don't throw, just log the error
    }
  }, []);

  // Check authentication status
  useEffect(() => {
    if (typeof window !== 'undefined') {
      const authStatus = localStorage.getItem('admin_authenticated');
      const authTimestamp = localStorage.getItem('admin_timestamp');
      
      if (authStatus === 'true' && authTimestamp) {
        const timeDiff = Date.now() - parseInt(authTimestamp);
        const oneHour = 60 * 60 * 1000; // 1 hour in milliseconds
        
        if (timeDiff < oneHour) {
          setIsAuthenticated(true);
        } else {
          // Session expired
          localStorage.removeItem('admin_authenticated');
          localStorage.removeItem('admin_timestamp');
        }
      }
    }
  }, []);

  const handleAdminAccess = () => {
    setIsAdminMode(true);
    setIsDeploymentMode(false);
    // Update URL without page reload
    if (typeof window !== 'undefined') {
      const url = new URL(window.location.href);
      url.searchParams.set('admin', 'true');
      url.searchParams.delete('deploy');
      window.history.pushState({}, '', url.toString());
    }
  };

  const handleDeploymentAccess = () => {
    setIsDeploymentMode(true);
    setIsAdminMode(false);
    // Update URL without page reload
    if (typeof window !== 'undefined') {
      const url = new URL(window.location.href);
      url.searchParams.set('deploy', 'true');
      url.searchParams.delete('admin');
      window.history.pushState({}, '', url.toString());
    }
  };

  const handleBackToPublic = () => {
    setIsAdminMode(false);
    setIsDeploymentMode(false);
    // Update URL without page reload
    if (typeof window !== 'undefined') {
      const url = new URL(window.location.href);
      url.searchParams.delete('admin');
      url.searchParams.delete('deploy');
      window.history.pushState({}, '', url.toString());
    }
  };

  const handleAuthenticated = () => {
    setIsAuthenticated(true);
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
    if (typeof window !== 'undefined') {
      localStorage.removeItem('admin_authenticated');
      localStorage.removeItem('admin_timestamp');
    }
    // Optionally redirect to public site
    handleBackToPublic();
  };

  // Deployment mode rendering
  if (isDeploymentMode) {
    return (
      <LanguageProvider>
        <div className="min-h-screen bg-background">
          {/* Header */}
          <div className="border-b bg-white/80 backdrop-blur-sm sticky top-0 z-50">
            <div className="max-w-7xl mx-auto px-6 py-4">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-4">
                  <h1 className="font-bold text-xl">Deployment Guide</h1>
                  <div className="flex items-center space-x-2">
                    <Button
                      variant="default"
                      size="sm"
                      onClick={() => setDeploymentView('steps')}
                    >
                      <Rocket className="w-4 h-4 mr-2" />
                      Steps
                    </Button>
                    <Button
                      variant="default"
                      size="sm"
                      onClick={() => setDeploymentView('checklist')}
                    >
                      <CheckSquare className="w-4 h-4 mr-2" />
                      Checklist
                    </Button>
                  </div>
                </div>
                
                <div className="flex items-center space-x-2">
                  <Link href="/">
                    <Button
                      variant="outline"
                      className="flex items-center space-x-2"
                    >
                      <Home className="w-4 h-4" />
                      <span>Back to Home</span>
                    </Button>
                  </Link>
                  <Button
                    variant="outline"
                    onClick={handleBackToPublic}
                    className="flex items-center space-x-2"
                  >
                    <span>Back to Site</span>
                  </Button>
                </div>
              </div>
            </div>
          </div>

          {/* Content */}
          <div className="py-8">
            {deploymentView === 'steps' ? <DeploymentSteps /> : <DeploymentChecklist />}
          </div>
        </div>
      </LanguageProvider>
    );
  }

  // Admin mode rendering
  if (isAdminMode) {
    if (!isAuthenticated) {
      return (
        <LanguageProvider>
          <div className="relative">
            <AdminAuth onAuthenticated={handleAuthenticated} />
            
            {/* Back to Public Site Button */}
            <div className="fixed top-4 right-4 z-50">
              <div className="flex items-center space-x-2">
                <Link href="/">
                  <Button
                    variant="outline"
                    className="flex items-center space-x-2 bg-white/90 backdrop-blur-sm"
                  >
                    <Home className="w-4 h-4" />
                    <span>Back to Home</span>
                  </Button>
                </Link>
                <Button
                  variant="outline"
                  onClick={handleBackToPublic}
                  className="flex items-center space-x-2 bg-white/90 backdrop-blur-sm"
                >
                  <span>Back to Site</span>
                </Button>
              </div>
            </div>
          </div>
        </LanguageProvider>
      );
    }

    return (
      <LanguageProvider>
        <AdminPanel onLogout={handleLogout} />
      </LanguageProvider>
    );
  }

  // Public site rendering
  return (
    <LanguageProvider>
      <div className="min-h-screen bg-white">
        <MinimalHeader />
        <main>
          <section id="hero">
            <Hero />
          </section>
          <section id="trust">
            <Trust />
          </section>
          <section id="calculator">
            <LoanCalculator />
          </section>
          <section id="how-it-works">
            <HowItWorks />
          </section>

          <section id="application">
            <ApplicationForm />
          </section>
          <section id="testimonials">
            <Testimonials />
          </section>
          <section id="contact">
            <Contact />
          </section>
        </main>
        <Footer />
        <SectionNavigator />
        
        {/* Hidden Access Buttons */}
        <div className="fixed bottom-4 left-4 z-50 space-y-2">
          <Button
            variant="ghost"
            size="sm"
            onClick={handleAdminAccess}
            className="opacity-0 hover:opacity-100 transition-opacity duration-300 block"
            title="Admin Access"
          >
            <Shield className="w-4 h-4" />
          </Button>
          <Button
            variant="ghost"
            size="sm"
            onClick={handleDeploymentAccess}
            className="opacity-0 hover:opacity-100 transition-opacity duration-300 block"
            title="Deployment Guide"
          >
            <Rocket className="w-4 h-4" />
          </Button>
        </div>
      </div>
    </LanguageProvider>
  );
}
