import {
  useState,
  useEffect,
  useCallback,
  useRef,
} from "react";

interface GoldRateData {
  rate24k: number;
  currency: string;
  lastUpdated: string;
  source: string;
  priceChangePercent?: number;
  usdToInr?: number;
}

interface UseGoldRatesReturn {
  goldRate: GoldRateData | null;
  loading: boolean;
  error: string | null;
  refetch: () => void;
}

export function useGoldRates(): UseGoldRatesReturn {
  const [goldRate, setGoldRate] = useState<GoldRateData | null>(
    null,
  );
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // Store latest values
  const goldPriceUSDRef = useRef<number>(2050); // PAXG price in USD
  const usdToInrRef = useRef<number>(83.5); // USD to INR rate
  const goldPriceChangeRef = useRef<number>(0); // 24h price change %
  
  // WebSocket references
  const goldWsRef = useRef<WebSocket | null>(null);
  const reconnectTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const reconnectAttemptsRef = useRef(0);
  
  // Polling interval for USD/INR (as backup to WebSocket)
  const usdInrPollingRef = useRef<NodeJS.Timeout | null>(null);

  // Calculate and update gold rate
  const updateGoldRate = useCallback(() => {
    const goldPriceUSD = goldPriceUSDRef.current;
    const usdToInr = usdToInrRef.current;
    const priceChangePercent = goldPriceChangeRef.current;

    // Convert troy ounce to grams (1 troy oz = 31.1035 grams)
    const TROY_OZ_TO_GRAMS = 31.1035;
    const goldPricePerGramUSD = goldPriceUSD / TROY_OZ_TO_GRAMS;

    // Convert USD to INR using LIVE rate
    const goldPricePerGramINR = goldPricePerGramUSD * usdToInr;

    console.log(
      `üí∞ Live Gold: $${goldPriceUSD.toFixed(2)}/oz | USD/INR: ‚Çπ${usdToInr.toFixed(2)} ‚Üí ‚Çπ${Math.round(goldPricePerGramINR)}/gram`,
    );

    setGoldRate({
      rate24k: Math.round(goldPricePerGramINR),
      currency: "INR",
      lastUpdated: new Date().toLocaleString("en-IN", {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
        second: "2-digit",
      }),
      source: "Live Real-Time Data (Binance)",
      priceChangePercent: priceChangePercent,
      usdToInr: usdToInr,
    });

    setError(null);
  }, []);

  // Fetch USD/INR from multiple sources (API-based real-time)
  const fetchUsdInrRate = useCallback(async () => {
    try {
      console.log("üîÑ Fetching live USD/INR rate from API...");
      
      // Try multiple sources in order of preference
      
      // Source 1: ExchangeRate-API (free, no key needed, updates hourly)
      try {
        const response = await fetch('https://api.exchangerate-api.com/v4/latest/USD');
        const data = await response.json();
        
        if (data && data.rates && data.rates.INR) {
          const rate = parseFloat(data.rates.INR);
          if (rate > 0) {
            console.log(`‚úÖ USD/INR from ExchangeRate-API: ‚Çπ${rate.toFixed(2)}`);
            usdToInrRef.current = rate;
            updateGoldRate();
            return;
          }
        }
      } catch (err) {
        console.warn("‚ö†Ô∏è ExchangeRate-API failed, trying next source...");
      }
      
      // Source 2: Fawaz Ahmed's Exchange Rate API (free, no key, updates daily)
      try {
        const response = await fetch('https://cdn.jsdelivr.net/npm/@fawazahmed0/currency-api@latest/v1/currencies/usd.json');
        const data = await response.json();
        
        if (data && data.usd && data.usd.inr) {
          const rate = parseFloat(data.usd.inr);
          if (rate > 0) {
            console.log(`‚úÖ USD/INR from Fawaz API: ‚Çπ${rate.toFixed(2)}`);
            usdToInrRef.current = rate;
            updateGoldRate();
            return;
          }
        }
      } catch (err) {
        console.warn("‚ö†Ô∏è Fawaz API failed, trying next source...");
      }
      
      // Source 3: Use Binance P2P API (real-time USDT/INR from Indian market)
      try {
        const response = await fetch('https://p2p.binance.com/bapi/c2c/v2/friendly/c2c/adv/search', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            asset: 'USDT',
            fiat: 'INR',
            merchantCheck: false,
            page: 1,
            rows: 1,
            tradeType: 'BUY',
            transAmount: '100',
          }),
        });
        
        const data = await response.json();
        
        if (data && data.data && data.data.length > 0) {
          const rate = parseFloat(data.data[0].adv.price);
          if (rate > 0) {
            console.log(`‚úÖ USD/INR from Binance P2P: ‚Çπ${rate.toFixed(2)}`);
            usdToInrRef.current = rate;
            updateGoldRate();
            return;
          }
        }
      } catch (err) {
        console.warn("‚ö†Ô∏è Binance P2P API failed, trying next source...");
      }
      
      // Fallback: Keep last known rate or use default
      console.warn("‚ö†Ô∏è All USD/INR sources failed, using last known rate:", usdToInrRef.current);
      
    } catch (err) {
      console.error("‚ùå Error fetching USD/INR rate:", err);
    }
  }, [updateGoldRate]);

  // Connect to PAXG/USDT WebSocket (Gold price in USD)
  const connectGoldWebSocket = useCallback(() => {
    try {
      console.log("üîÑ Connecting to Binance for PAXG/USDT (Gold Price)...");

      if (goldWsRef.current) {
        goldWsRef.current.close();
      }

      const ws = new WebSocket(
        "wss://stream.binance.com:9443/ws/paxgusdt@ticker",
      );

      ws.onopen = () => {
        console.log("‚úÖ Gold Price WebSocket connected");
        setLoading(false);
        reconnectAttemptsRef.current = 0;
      };

      ws.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);
          const goldPriceUSD = parseFloat(data.c); // Current price
          const priceChangePercent = parseFloat(data.P); // 24h change

          if (goldPriceUSD && goldPriceUSD > 0) {
            goldPriceUSDRef.current = goldPriceUSD;
            goldPriceChangeRef.current = priceChangePercent;
            updateGoldRate(); // Recalculate with latest USD/INR
          }
        } catch (parseError) {
          console.error("‚ùå Error parsing gold price:", parseError);
        }
      };

      ws.onerror = (error) => {
        console.error("‚ùå Gold WebSocket error:", error);
      };

      ws.onclose = (event) => {
        console.log(`üîå Gold WebSocket closed (${event.code})`);
        
        // Attempt to reconnect
        if (reconnectAttemptsRef.current < 5) {
          const delay = Math.min(
            1000 * Math.pow(2, reconnectAttemptsRef.current),
            30000,
          );
          console.log(
            `üîÑ Reconnecting in ${delay / 1000}s... (attempt ${reconnectAttemptsRef.current + 1}/5)`,
          );

          reconnectTimeoutRef.current = setTimeout(() => {
            reconnectAttemptsRef.current++;
            connectGoldWebSocket();
          }, delay);
        } else {
          console.warn("‚ö†Ô∏è Max reconnection attempts reached, using fallback");
          setError("Live feed unavailable - using market rate");
          useFallbackRate();
        }
      };

      goldWsRef.current = ws;
    } catch (err) {
      console.error("‚ùå Failed to create Gold WebSocket:", err);
    }
  }, [updateGoldRate]);

  // Start USD/INR polling (fetch every 10 seconds for real-time updates)
  const startUsdInrPolling = useCallback(() => {
    console.log("üîÑ Starting USD/INR real-time polling (every 10 seconds)...");
    
    // Fetch immediately
    fetchUsdInrRate();
    
    // Then poll every 10 seconds
    if (usdInrPollingRef.current) {
      clearInterval(usdInrPollingRef.current);
    }
    
    usdInrPollingRef.current = setInterval(() => {
      fetchUsdInrRate();
    }, 10000); // 10 seconds
    
  }, [fetchUsdInrRate]);

  const connectWebSocket = useCallback(() => {
    console.log("üöÄ Initializing real-time gold rate system...");
    connectGoldWebSocket();
    startUsdInrPolling();
  }, [connectGoldWebSocket, startUsdInrPolling]);

  const useFallbackRate = useCallback(() => {
    console.log(
      "üìç Using market-based fallback rate: ‚Çπ7,200/gram",
    );
    setGoldRate({
      rate24k: 7200,
      currency: "INR",
      lastUpdated: new Date().toLocaleString("en-IN", {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
      }),
      source: "Market Rate",
      usdToInr: 83.5,
    });
    setLoading(false);
  }, []);

  const refetch = useCallback(() => {
    console.log(
      "üîÑ Manual refresh triggered - reconnecting...",
    );
    reconnectAttemptsRef.current = 0;
    connectWebSocket();
  }, [connectWebSocket]);

  // Initial connection
  useEffect(() => {
    connectWebSocket();

    // Cleanup on unmount
    return () => {
      if (goldWsRef.current) {
        goldWsRef.current.close();
        goldWsRef.current = null;
      }
      if (reconnectTimeoutRef.current) {
        clearTimeout(reconnectTimeoutRef.current);
      }
      if (usdInrPollingRef.current) {
        clearInterval(usdInrPollingRef.current);
      }
    };
  }, [connectWebSocket]);

  return {
    goldRate,
    loading,
    error,
    refetch,
  };
}
